@layer base {
  :root {
    --background: #F8FAFC;
    --foreground: #475569;
    --cyan: #06B6D4;
    --border: #e2e8f0;
  }
  .dark {
    --background: #020408;
    --foreground: #cbd5e1;
    --cyan: #00F0FF;
    --border: rgba(255, 255, 255, 0.1);
  }
  * {
    border-color: var(--color-border);
  }
  body {
    background-color: var(--color-background);
    font-family: var(--font-sans);
    color: var(--color-foreground);
  }
  h1 {
    font-family: var(--font-sans);
    font-weight: 300;
  }
  h2, h3 {
    font-family: var(--font-sans);
    font-weight: 700;
  }
  p {
    font-family: var(--font-sans);
    font-weight: 400;
  }
  button {
    cursor: pointer;
  }
  button, a, nav a {
    font-family: var(--font-sans);
